import anime from 'animejs';

export default function() {
  let $screamPage = $('#scream-page');
  let $theStory = $screamPage.find('.the-story');
  let $content = $screamPage.find('.content-section');

  $('.read-btn').on('click', function() {
    $screamPage.addClass('show-content');
    $('.btn-div').css('overflow', 'visible');
    setTimeout(reveal, 400);
  });

  $('.bottom-btn').on('click', function() {
    $screamPage.removeClass('show-content');
    conceal();
  });

  // $win.on('menuChange', function(e, show) {
  //   show = !!show;
  //   $screamPage.toggleClass('show-content', show);
  //   !show && conceal();
  //   $('.btn-div').css('overflow', '');
  // });

  return {
    $root: $screamPage,
    times: {title: 200, page: 20},
    activate: function() {

    },
    deactivate: function() {

    },
  };

  function conceal() {
    anime({
      targets: [$content[0], $content.find('.container')[0]],
      opacity: [1, 0],
      easing: 'easeOutQuad',
      duration: 1200,
    });
    anime({
      targets: '#scream-read',
      opacity: [0, 0.75],
      easing: 'linear',
      duration: 2000,
    });
    anime({
      targets: '#scream-read svg',
      scale: [170, 1],
      easing: 'linear',
      duration: 2000,
    });
  }

  function reveal() {
    showText($theStory[0], 0);
    showText($theStory[1], 1200);
    showText($theStory[2], 2500);
    opacityEaseInQuad($content[0], 4000);
    opacityEaseInQuad($content.find('.container')[0], 6000);

    anime({
      targets: '#scream-read',
      opacity: [0.75, 0],
      easing: 'linear',
      duration: 7000,
    });

    anime({
      targets: '#scream-read svg',
      scale: [1, 170],
      easing: 'linear',
      duration: 7000,
    });

    function opacityEaseInQuad(el, delay) {
      anime({
        targets: el,
        opacity: [0, 1],
        easing: 'easeInQuad',
        duration: 1200,
        delay,
      });
    }

    function showText(el, delay = 0) {
      anime({
        targets: el,
        scale: [0.05, 9],
        opacity: [0, 1],
        easing: 'easeInCirc',
        duration: 2000,
        delay,
      });
      setTimeout(function() {
        anime({
          targets: el,
          opacity: [1, 0],
          scale: [9, 70],
          easing: 'easeOutSine',
          duration: 3200,
        });
      }, 2000 + delay);
    }
  }
}

