import setup from 'config/setup.js';
import anime from 'animejs';
import {disableTimelines, addProgressLinster} from 'core/timeliner.js';
import getHome from 'pages/home.js';
import getScream from 'pages/scream.js';
import getStatic from 'pages/static.js';
import {fixSizes} from 'helpers/dom-helpers';

export default function() {
  var allowScroll = 1;
  var $nav = $('#nav');
  var navData =  {
    navProgress: 0,
  };
  var navTimeline = anime.timeline({
    duration: 100,
    autoplay: false,
    easing: 'linear',
  }).add({
    targets: '#nav-btn',
    rotate: [0, '-315deg'],
    duration: 100,
  }).add({
    targets: '#nav .content, #nav .overlay',
    translateX: [0, '-100%'],
    duration: 100,
  }, 0);
  var pages = {
    home: getHome(),
    'hi-scream': getScream(),
    about: getStatic('about'),
    create: getStatic('create'),
    content: getStatic('content'),
    contact: getStatic('contact'),
    production: getStatic('production'),
    'dj-snake': getStatic('snake', {title: 2020, page: 800}),
    ibiza: getStatic('ibiza', {title: 20, page: 200}),
    'an-ant-for-ants': getStatic('ants', {title: 20, page: 200}),
    'institut-du-monde': getStatic('institut', {title: 20, page: 200}),
    notfound: getStatic('404', {title: 20, page: 200}),
  };
  var lastPage;

  window.onpopstate = handleRoute;

  addProgressLinster('home', function(progress, {navParams}) {
    if (allowScroll) {
      navData.navProgress = navParams.prog;
      navTimeline.seek(navData.navProgress);
      $nav.find('.reveal').toggleClass('show', navData.navProgress > 0);
    } else if (navParams.prog < 1) {
      allowScroll = 1;
    }
  });

  $('[data-href]').each(function() {
    let $el = $(this);
    let href = $el.data('href');
    if (href[0] != '/') {
      href = '/' + href;
    }
    href = href + setup.extension();
    $el.on('click', function(e) {
      e.preventDefault();
      window.history.pushState({}, document.title, href);
      handleRoute($el.closest('.footer').length);
    }).css('cursor', 'pointer').attr('href', href);
  });

  handleRoute();

  function handleRoute(slide) {
    let route = document.location.pathname.replace(/^\/|\/$|\.html$/g, '') || 'home';
    let page = pages[route] || pages.notfound;
    toggleMenu(false);
    if (page != lastPage) {
      disableTimelines();
      lastPage && lastPage.deactivate();
      showPage(page, slide);
      lastPage = page;
    }
  }

  $nav.find('#nav-btn').on('click', function() {
    $('.work-dropdown').css('height', '').removeClass('open');
    toggleMenu(navData.navProgress == 0 || (navData.navProgress >= 50 && navData.navProgress < 99));
  });

  $nav.find('.work-btn').on('click', function() {
    let $dropdown = $('.work-dropdown').toggleClass('open');
    $dropdown.css('height', $dropdown.hasClass('open') ? $('.work-dropdown-inner').height() + 10 : 0);
    return false;
  });

  function toggleMenu(open) {
    anime({
      targets: navData,
      navProgress: open ? 100 : 0,
      easing: 'easeInOutQuart',
      duration: 1200,
      update: function() {
        navTimeline.seek(navData.navProgress);
      }
    });
    $nav.find('.reveal').toggleClass('show', open);
    allowScroll = 0;
  }

  function showPage(page, slide) {
    let $page = page.$root;
    let $main = $page.find('.main').removeClass('show');
    let $intro = $page.find('.intro').css({opacity: '', transform: ''});
    let $bg = $page.find('.page-bg').css({transform: '', opacity: ''});
    let $oldPage = $('.page.show');
    $main.find('.right').css({opacity: ''});
    $page.find('.reveal').removeClass('show');
    if (lastPage && slide) {
      return slidePage({page, $oldPage, $page, $intro, $main, $bg});
    }
    $doc.scrollTop(0);
    if ($oldPage.length) {
      anime({
        targets: $oldPage.css({zIndex: 12})[0],
        opacity: [1, 0],
        duration: 800,
        easing: 'linear',
        complete: function() {
          $oldPage.removeClass('show ready').css({zIndex: -1, opacity: ''});
        }
      });
      anime({
        targets: $page[0],
        opacity: [0, 1],
        duration: 800,
        easing: 'linear',
      });
      displayPage({page, $page, $intro, $main, $bg});
    } else {
      $('.page').css('opacity', '');
      setTimeout(displayPage, page.times.page, {page, $page, $intro, $main, $bg});
    }
  }

  function slidePage({page, $oldPage, $page, $intro, $main, $bg}) {
    let $detail = $oldPage.find('.detail-section').css({position: 'fixed', top: 'auto'});
    let $others = $oldPage.find('>*').not($detail).css('display', 'none');
    $page.css({zIndex: 11, opacity: 1}).addClass('show ready');
    displayPage({page, $page, $intro, $main, $bg});
    $oldPage.css({zIndex: 12, background: 'none', position: 'fixed'});
    $doc.scrollTop(1);
    anime({
      targets: $detail[0],
      bottom: [0, $win.height()],
      duration: 1500,
      easing: 'linear',
      complete: function() {
        $oldPage.removeClass('show ready').css({zIndex: -1, background: '', position: ''});
        $detail.css({bottom: '', position: '', top: ''});
        $others.css('display', '');
      }
    });
  }

  function displayPage({page, $page, $intro, $main, $bg}) {
    let triTop = $page.find('.tri-wrap.top')[0];
    $page.addClass('show').css({zIndex: 11});
    $main.addClass('show');
    $page.find('.services-outer').css('height', '').removeClass('active');
    $page.find('.services-btn').removeClass('active');
    fixSizes();
    if ($intro[0]) {
      anime({
        targets: $intro[0],
        opacity: [0, 1],
        duration: 800,
        delay: $intro.data('time') || 800,
        easing: 'easeInSine'
      });
    }
    anime({
      targets: $bg[0],
      opacity: $bg.data('opacity'),
      scale: $bg.data('scale'),
      duration: 3300,
      easing: 'linear',
    });
    triTop && anime({
      targets: [triTop, $page.find('.tri-wrap.bottom')[0]],
      opacity: [0.8, 0.4],
      scale: [2, 1],
      duration: 3500,
      easing: 'linear',
    });
    page.activate();
    setTimeout(function() {
      $page.find('.reveal').addClass('show');
      $doc.scrollTop(0);
      setTimeout(function() {
        $main.find('.left').addClass('show');
        $page.addClass('ready');
        anime({
          targets: $main.find('.right')[0],
          opacity: [0, 1],
          duration: 800,
          delay: 1000,
          easing: 'easeInSine',
        });
      }, 1000);
    }, page.times.title);
  }
}

