var setup = {
  templateUrls: function(urls) {
    return urls.map(x => '/templates/' + x + '.html?t=' + setup.timestamp());
  },
  init: function(obj) {
    $.each(obj, function(key, value) {
      if (setup[key]) {
        throw 'Duplicate key: ' + key;
      }
      setup[key] = function() {
        return value;
      };
    });
  },
  useVideo: function() {
    return navigator.vendor && navigator.vendor.match(/apple/i) && !navigator.userAgent.match(/crios/i) && !navigator.userAgent.match(/fxios/i);
  },
};

export default setup;
