function carousel() {
  let $section = $(this);
  let $slides = $section.find('.slide');
  let $dots = createDots();
  let index = -1;
  select(0);
  $section.find('.nav-buttons .btn').on('click', function() {
    let dir = $(this).data('arg');
    let newIndex = index + dir < 0 ? $slides.length - 1 : index + dir > $slides.length - 1 ? 0 : index + dir;
    $slides.eq(index).css('left', -102 * dir + '%');
    $slides.eq(newIndex).addClass('stop').css('left', 102 * dir + '%');
    setTimeout(function() {
      $slides.eq(newIndex).removeClass('stop').css('left', 0);
    });
    index = newIndex;
    $dots.removeClass('active').eq(index).addClass('active');
  });

  $dots.on('click', function() {
    select($(this).index());
  });

  function createDots() {
    let $btns = $section.find('.buttons');
    for (var i = 0; i < $slides.length; i++) {
      $btns.append('<div class="dot"></div>');
    }
    return $btns.children();
  }

  function select(newIndex) {
    if (index != newIndex) {
      index = newIndex;
      for (let i = 0; i < $slides.length; i++) {
        $slides.eq(i).css('left', (i - index) * 102 + '%');
      }
      $dots.removeClass('active').eq(index).addClass('active');
    }
  }
}

$.fn.carousel = function () {
  return this.each(carousel);
};
