import home from 'data/home.json';
import sequences from 'data/sequences.json';
import {resetTimeline} from 'core/timeliner.js';

let map = {
  home,
};

export function getSequences() {
  return sequences;
}

export function getTimeData(name) {
  return map[name];
}

export function setTimeData(timedata) {
  map[timedata.id] = timedata;
  resetTimeline(timedata.id);
}

export function getNames() {
  return Object.keys(map);
}
