import timeliner, {addProgressLinster} from 'core/timeliner.js';
import {getWinWidth, getWinHeight} from 'helpers/app-helpers.js';
import {getTimeData} from 'core/scroll-data.js';

import getEmotion from './home/emotion.js';
import getEntrance from './home/entrance.js';
import getCanvas from './home/home-canvas.js';
import getStimulate from './home/stimulate.js';
import getStory from './home/story.js';

export default function() {
  let $home = $('#home-page');
  let emotion = getEmotion();
  let entrance = getEntrance();
  let homeCanvas = getCanvas();
  let stimulate = getStimulate();
  let story = getStory();

  addProgressLinster('home', function(progress, targets) {
    emotion.seek(progress, targets);
    homeCanvas.seek(progress, targets);
    stimulate.seek(progress, targets);
    story.seek(progress, targets);
    entrance.seek(progress);
  });

  function setup() {
    let opts = {winHeight: getWinHeight(), winWidth: getWinWidth()};
    homeCanvas.setup(opts);
    story.setup(opts);
    entrance.setup(opts);
  }

  return {
    $root: $home,
    times: {title: 2020, page: 800},
    activate: function() {
      setup();
      timeliner($home, getTimeData('home'));
      $win.on('resize.home', setup);
    },
    deactivate: function() {
      $win.off('.home');
    }
  };
}
