import {updateWatches} from 'setjs/template/binding.js';
import {addAction} from 'core/acts-funcs.js';
import showTimeline from 'configurator/timeline-panel.js';
import showWaterfall from 'configurator/waterfall.js';
import {getTimeData, setTimeData} from 'core/scroll-data.js';

function createDownloadUrl(timedata) {
  timedata.timestamp = Date.now();
  return URL.createObjectURL(new Blob([JSON.stringify(timedata, null, 2)], {
    type: 'text/json;encoding:utf-8',
  }));
}

function download({comp}) {
  let timedata = getTimeData(comp.data.config.id);
  comp.$downloadBtn.attr({download: timedata.id + '.json', href: createDownloadUrl(timedata)})[0].click();
}

function closePanel() {
  $('.side-panel').remove();
  $body.removeClass('panel-open');
  updateWatches();
}

function panelFunc(func) {
  return function(opts) {
    closePanel();
    func(opts);
  };
}

addAction('closePanel', closePanel);
addAction('download', download);

addAction('showTimeline', panelFunc(showTimeline));
addAction('showWaterfall', panelFunc(showWaterfall));

addAction('import', function(opts) {
  var file = opts.e.target.files[0];
  if (!file) return;
  var reader = new FileReader();
  reader.onload = function(e) {
    closePanel();
    setTimeData(JSON.parse(e.target.result));
  };
  reader.readAsText(file);
});



