import anime from 'animejs';
import {setDefData} from 'setjs/kernel/basics.js';
import {addAction} from 'core/acts-funcs.js';
import {resetTimeline} from 'core/timeliner.js';
import {getWinHeight} from 'helpers/app-helpers.js';

var previous;

function playTimeline(config, start, end) {
  var winHeight = getWinHeight();
  var height = config.$container.outerHeight() - winHeight;
  var target = {time: start};
  resetTimeline(config.id);

  previous = {
    target,
    anim: anime({
      targets: target,
      time: end,
      duration: height * (end - start) / 100,
      easing: 'linear',
      complete: function() {
        setDefData('animPlaying', false);
        previous = 0;
      },
      update: function() {
        $doc.scrollTop(config.$container.offset().top + target.time / 100 * height);
        setDefData('progress', config.id + ':' + target.time.toFixed(1));
      }
    }),
  };
}

function stopPlayer() {
  setDefData('animPlaying', false);
  if (previous) {
    previous.anim.remove(previous.target);
    previous = 0;
  }
}

addAction('toggleAnim', function(opts) {
  var full = opts.data.field ? 0 : 100;
  if (previous) {
    stopPlayer();
  } else {
    setDefData('animPlaying', true);
    playTimeline(opts.data.rd.config || opts.data.config, full ? 0 : opts.data.field.time[0], full || opts.data.field.time[1]);
  }
});
