import anime from 'animejs';

var lines = [
  [{txt: 'Passion ', h: 1, so: 0, sd: 11, eo: 20, ed: 11}, {txt: 'fuels my work', so: 8, sd: 8, eo: 19, ed: 8}],
  [{txt: 'As well as ', so: 32, sd: 8, eo: 50, ed: 8}, {txt: 'Ignites', h: 1, so: 36, sd: 11, eo: 55, ed: 11}, {txt: ' my creativity', so: 41, sd: 8, eo: 55, ed: 8}],
  [{txt: 'in ', so: 67, sd: 8, eo: 85, ed: 8}, {txt: 'everything', h: 1, so: 68, sd: 11, eo: 89, ed: 11}, {txt: ' I undertake.', so: 73, sd: 8, eo: 85, ed: 8}],
];


