// }

function createSentences($parent) {
  let sentences = [];
  lines.forEach(function(phrases) {
    let list = [];
    let $sentence = $('<div class="sentence">').appendTo($parent);
    phrases.forEach(function(phrase) {
      let $el = $(`<span>${phrase.txt}</span>`).addClass(phrase.h ? 'highlight' : 'normal');
      $sentence.append($el);
      list.push($.extend({el: $el[0]}, phrase));
    });
    sentences.push(list);
  });
  return sentences;
}

export default function() {
  let emotionVid = $('#emotion-video')[0];
  let sentences = createSentences($('#emotion-section'));
  let timeline = anime.timeline({
    autoplay: false,
    easing: 'linear',
  });

  sentences.forEach(function(sentence) {
    sentence.forEach(addSequence);
  });

  function addSequence({el, so, sd, eo, ed}) {
    timeline.add({
      targets: el,
      duration: sd,
      opacity: [0, 1]
    }, so);

    timeline.add({
      targets: el,
      duration: ed,
      opacity: [1, 0]
    }, eo);
  }

  return {
    seek: function(progress, {emotion, top_black}) {
      timeline.seek(emotion.m * timeline.duration);
      if (top_black.emotion_show) {
        if (emotionVid.paused) {
          emotionVid.muted = true;
          emotionVid.play();
        }
      } else if (!emotionVid.paused) {
        emotionVid.pause();
      }
    }
  };
}
