import setup from 'config/setup.js';
import initialiseSetjs from 'core/setjs-init.js';
import initConfigurator from 'Configurator';
import loader from 'core/loader.js';
import {initTimeline} from 'core/timeliner.js';

import initNav from 'core/nav.js';
import triangles from 'helpers/triangles.js';
import domHelpers from 'helpers/dom-helpers';

import 'plugins/jquery/carousel.js';

window.$win = $(window);
window.$doc = $(document);
window.$body = $('body');

$(function() {
  setup.init(APP_SETTINGS);
  initialiseSetjs();
  if ('scrollRestoration' in history) {
    history.scrollRestoration = 'manual';
  }
  loader(function() {
    loadTemplates();
    initTimeline();
    triangles();
    domHelpers();
    initNav();
    initConfigurator();
  });
});

function loadTemplates() {
  let html = $('#page-templates').html();
  $('#page-templates').remove();
  $('body').append($(html).filter(function(i, el) {
    return el.outerHTML && !$('#' + el.id).length;
  }));
}


