export default function(id, times = {title: 2020, page: 800}) {
  let $page = $('#' + id + '-page');
  let $slides = $page.find('.carousel-slides');
  let $carouselTop =  $page.find('.carousel-top');
  let carousel = $page.find('.carousel-wrap')[0];

  function resize() {
    $('#intro-extra').appendTo($win.width() > 768 ? '#snake-intro' : '#carousel-inner');
  }

  function scroll() {
    let bounds = carousel.getBoundingClientRect();
    let winHeight = $win.height();
    let progress = Math.min(1, Math.max(0, (bounds.top) / (winHeight + bounds.top - bounds.bottom)));
    $slides.css('left', $win.width() - $slides.outerWidth() * progress);
    $carouselTop.css('transform', `translateY(${Math.max(0, bounds.top)}px)`);
  }

  return {
    $root: $page,
    times,
    activate: function() {
      resize();
      $win.on('resize.static', resize);
      if ($slides.length) {
        scroll();
        $win.on('scroll.static', scroll);
      }
    },
    deactivate: function() {
      $win.off('.static');
    },
  };
}
