import anime from 'animejs';

let text = 'I AM THE STORY BEHIND THE CODE';

export default function() {
  let $section = $('#story-section').append(text.split(' ').map(x => $(`<p class="word"><b>${x}</b></p>`).addClass(x == 'BEHIND' ? 'l' : '')));
  let $words = $section.find('.word');
  let step = 1 / $words.length;
  let timeline = anime.timeline({
    autoplay: false,
    easing: 'linear',
  });
  let fontSize;

  return {
    setup: function({winWidth, winHeight}) {
      fitWordWidth($section.css('font-size', fontSize = 60).find('.l b'));
      let wordHeight = $words.eq(0).height();
      let wTop = -winHeight / 2 - wordHeight / 2 - 10;
      let frac = -wordHeight / wTop * 0.75;
      wTop = wTop + 'px';
      $words.each(function(index, word) {
        timeline.remove(word);
        $(word).css({transform: '', opacity: ''});
      });

      $words.each(function(index, word) {
        let firstOffset = index * step * frac;
        let secondOffset = step + index * step * frac;
        addStep({opacity: [0, 1], easing: 'easeInCubic'}, firstOffset, 0.9);
        addStep({translateY: [0, wTop]}, firstOffset);

        if (index < $words.length - 1) {
          addStep({opacity: [1, 0], easing: 'easeOutQuart'}, secondOffset);
          addStep({translateY: [wTop, -winHeight - wordHeight + 'px']}, secondOffset);
        } else {
          addStep({scale: [1, 14], easing: 'easeInQuad'}, step * 0.4 + secondOffset, 0.6);
          addStep({opacity: [1, 0], easing: 'easeInQuad'}, step * 0.2 + secondOffset, 0.8);
        }

        function addStep(props, offset, mul = 1) {
          timeline.add($.extend({
            targets: word,
            duration: step * mul,
          }, props), offset);
        }
      });

      function fitWordWidth($word) {
        while ($word.width() < winWidth * 0.96 && fontSize < 400) {
          $section.css('font-size', fontSize++);
        }
      }
    },
    seek: function(progress, {story}) {
      timeline.seek(story.m * timeline.duration);
    }
  };
}
