import setup from 'config/setup.js';
import {loadImages} from 'setjs/utility/assets.js';
import {batchCall} from 'setjs/utility/calls.js';

var imgAssets;
var videos;
var $progress = $('#progress-bar');

export function getImage(name) {
  return imgAssets[name] && imgAssets[name].img;
}

export function getVideo(name) {
  return videos[name];
}

export default function(done) {
  var imagesToLoad = [{
    id: 'usain',
    url: '/images/usainbolt.png',
  }];

  var bc =  batchCall({
    success: function(assets) {
      imgAssets = assets.images;
      videos = assets.videos || {};
      done();
      setTimeout(function() {
        $progress.css('width', '100%');
        $progress.animate({'opacity': 0}, 1200, function() {
          $progress.remove();
        });
      }, 2000);
    },
    error: function() {
      alert('There was an error. Please try later.');
    },
  }).add(imageLoader, null, 'images');

  if (setup.useVideo()) {
    bc.add(videoLoader, null, 'videos');
  } else {
    imagesToLoad.push({
      id: 'logo',
      url: '/images/vmlogoforparticles.png',
    });
  }

  bc.go();

  function imageLoader(opts) {
    loadImages(imagesToLoad, opts.success, function(prog) {
      $progress.css('width', 20 + (60 * prog) + '%');
    });
  }

  function videoLoader(opts) {
    let $vmVideo = $(`<video id="particles-vid" autoplay playsinline muted src="/videos/particles.mp4"></video>`);
    $vmVideo.on('loadeddata', function() {
      if($vmVideo[0].readyState >= 2) {
        opts.success({$vmVideo});
      }
    });
  }
}
