import getComp from 'setjs/template/component.js';

export default function(opts) {
  let alertComp = getComp('config/alert-box', opts, {
    oK: function() {
      alertComp.$root.remove();
      opts.ok();
    },
    no: function() {
      alertComp.$root.remove();
      opts.no();
    },
    cancel: function() {
      alertComp.$root.remove();
    }
  });
  $body.append(alertComp.$root);
}
