import anime from 'animejs';
import {debounce} from 'setjs/utility/calls.js';
import {getWinHeight} from 'helpers/app-helpers.js';
import {getVal} from 'setjs/utility/array.js';
import {getSequences} from 'core/scroll-data.js';
import {timelineUpdate} from 'Configurator';
import {getTimeData} from 'core/scroll-data.js';

var cssProps = ['top', 'left', 'opacity', 'fontSize', 'transform', 'width', 'height'];
var progressListeners = {};
var timeConfigs = [];

function seek() {
  let winHeight = $win.height();

  timeConfigs.forEach(config => {
    let bounds = config.$container[0].getBoundingClientRect();
    let progress = Math.min(1, Math.max(0, (bounds.top) / (winHeight + bounds.top - bounds.bottom)));
    config.timeline.seek(config.duration * progress);
    (progressListeners[config.id] && progressListeners[config.id]).forEach(listener => {
      listener && listener(100 * progress, config.targets);
    });
  });
}

export function resetTimeline(id) {
  let index = timeConfigs.findIndex(x => x.id == id);
  timeConfigs[index] = createTimeline(timeConfigs[index].$container, getTimeData(id));
}

function resetTimelines() {
  for (var i = 0; i < timeConfigs.length; i++) {
    timeConfigs[i] = createTimeline(timeConfigs[i].$container, getTimeData(timeConfigs[i].id));
  }
}

export function initTimeline() {
  $win.on('resize', debounce(resetTimelines));
  $doc.on('scroll', seek);
}

export function addProgressLinster(name, func) {
  progressListeners[name] = progressListeners[name] || [];
  progressListeners[name].push(func);
}

export function disableTimelines() {
  timeConfigs.length = 0;
  timelineUpdate(timeConfigs);
}

export default function($container, timedata) {
  timeConfigs.push(createTimeline($container, timedata));
  timelineUpdate(timeConfigs);
}

function createTimeline($container, timedata) {
  let winHeight = getWinHeight();
  let objTargets = {};
  let duration = timedata.pages * winHeight || $container.outerHeight();
  let timeline = anime.timeline({
    duration,
    autoplay: false,
    easing: 'linear',
  });
  timedata.pages && $container.css('height', duration + winHeight);

  timedata.obj.forEach(function(target) {
    createTargets(target, 0, objTargets[target.el] = {});
  });

  timedata.dom.forEach(function(target) {
    let $el = $(target.el);
    cssProps.forEach(prop => {
      $el.css(prop, '');
    });
    createTargets(target, $el);
  });
  return {id: timedata.id, $container, timeline, duration, targets: objTargets};

  function createTargets(target, $el, animObj) {
    targetFields(target).filter(field => ((field.browser || -1) & getBrowserFlag($container.width()))).forEach(function(field) {
      let item = {};
      let listItems = field.list.filter(x => x.key != 'sequence');
      if (!listItems.length) return;
      listItems.forEach(function(config) {
        let unit = config.unit || 0;
        item[config.key] = Array.isArray(config.val) ? config.val.slice().map(x => x + unit) : isNaN(config.val) ? config.val : config.val + unit;
        if (animObj) {
          animObj[config.key] = Array.isArray(config.val) ? config.val[0] : config.val;
        }
      });
      item.targets = animObj || $el.toArray();
      item.duration = (field.time[1] - field.time[0]) / 100 * duration;
      timeline.add(item, (field.time[0]) / 100 * duration);
      if (animObj) {
        animObj.start = Math.min(field.time[0], animObj.start || 100);
        animObj.end = Math.max(field.time[1], animObj.end || 0);
      }
    });
  }
}

function targetFields(target) {
  let fields = [];
  target.fields.forEach(function(field) {
    fields.push(field);
    field.list.forEach(function(item) {
      if (item.key == 'sequence') {
        let seqFields = getVal(getSequences(), item.val, 'el').fields;
        let mul = (field.time[1] - field.time[0]) / 100;
        seqFields.forEach(function(sField) {
          fields.push({
            time: [field.time[0] + sField.time[0] * mul, field.time[0] + sField.time[1] * mul],
            list: sField.list,
          });
        });
      }
    });
  });
  return fields;
}

function getBrowserFlag(containerWidth) {
  if (containerWidth <= 720) {
    return 1;
  } else if (containerWidth <= 1024) {
    return 2;
  }
  return 4;
}
