export default function() {
  let stimulateVid = $('#stimulate-video')[0];

  return {
    seek: function(progress) {
      if (progress > 4 && progress < 34) {
        if (stimulateVid.paused) {
          stimulateVid.muted = true;
          stimulateVid.play();
        }
      } else {
        stimulateVid.pause();
      }
    },
  };
}
