import {debounce} from 'setjs/utility/calls.js';
import {roundNum} from 'setjs/utility/numbers.js';

export default function() {
  fontSizes();
  fixSizes();
  $win.on('resize', debounce(fixSizes));

  $('.carousel-section').carousel();

  $('.white-tri').on('click', function() {
    $('html, body').animate({scrollTop: ($(this).data('factor') || 1) * $win.height()}, 2500);
  });

  $('.services-btn').on('click', function() {
    let $btn = $(this).toggleClass('active');
    let $target = $btn.closest('.page-section').find('.services-outer').toggleClass('active');
    $target.css('height', $target.hasClass('active') ? $target.find('.services').height() + 6 : '');
  });

  $win.on('scroll', function() {
    let height = $win.height();
    $('.white-tri').toggleClass('disable', $doc.scrollTop() > height);
    $('.scream-tri').css('opacity', 0);
    $('.page.show').find('.scream-tri').css('opacity', (height - Math.min(height, window.scrollY)) / height);
    $('[data-scroll]').each(function() {
      let $el = $(this);
      let opts = $el.data('scroll');
      let top = this.getBoundingClientRect().top + 120;
      let progress = Math.min(1, Math.max(0, (opts.factor || 1) * (height - top) / height));
      $el.css({
        opacity: opts.opacity ? opts.opacity[0] + progress * (opts.opacity[1] - opts.opacity[0]) : '',
        transform: opts.scale ? `scale(${opts.scale[0] + progress * (opts.scale[1] - opts.scale[0])})` : ''
      });
    });
  });
}

export function fixSizes() {
  let winWidth = $win.width();
  $('.video iframe').each(function(i, el) {
    el.style.maxHeight = Math.floor(winWidth * el.height / el.width) + 'px';
  });
  $('.tri-wrap.bottom').each(function() {
    let $wrapper = $(this);
    let width = $wrapper.width();
    $wrapper.css('margin-left', `${width > winWidth ? (winWidth - width) / 2 : 0}px`);
  });

  if ($('#institut-page').hasClass('show')) {
    let $parent = $('#institut-carousel');
    $parent.find('.nav-buttons').css('height', $parent.find('.carousel img').height());
  }
}

function fontSizes() {
  $('head').append(`<style>${createSize('lg', 5)}${mediaSize('md', 4, 1024)}${mediaSize('sm', 3, 768)}${mediaSize('xs', 2, 594)}${mediaSize('xxs', 2, 480)}</style>`);

  function createSize(prefix, max) {
    let styles = [];
    for (var i = 0.9; i <= max; i = roundNum(i + 0.1, 1)) {
      styles.push(`.${prefix}-${('' + (i)).replace('.', '-')}{font-size:${i}rem!important;}`);
    }
    return styles.join('\n') + '\n';
  }

  function mediaSize(prefix, max, res) {
    return `@media only screen and (max-width: ${res}px) {\n${createSize(prefix, max)}\n}`;
  }
}
